import React, { Component } from "react";
import { withRouter } from "react-router-dom";
import ClientService from "../../../../services/ClientService";
import TransactionService from "../../../../services/TransactionService";
import LoanService from "../../../../services/LoanService";
import LoanSchedule from "./LoanSchedule"
import Modal from '@material-ui/core/Modal';
import { linearLoan, lumpSumLoan, payDayLoan } from "../helpers/scheduleCalc";
import moment from 'moment'

class CreateLoan extends Component {
  constructor(props) {
    super(props);
    this.state = {
      _id: "",
      collateralType: "",
      collateralValue: 0,
      collateralDescription: "",
      startDate: "",
      open: false, 
      loanDuration: 0,
      loanAmount: 0,
      loanInterest: 0,
      loanType: "linear",
      loanPeriod: "monthly",
      payment: 0,
      interestPmt: 0,
      capitalPmt: 0,
      loanFinalPayment: 0,
      loanInterestFinalPayment: 0,
      loanCapitalFinalPayment: 0,
      investor: "",
      useOfFunds: "",
      cashAccounts: ["PLPERU", "REMPERU", "GCUS", "GFUS"],
      investmentAmount: 0,
      investors: []
    };
    this.ClientService = new ClientService();
    this.TransactionService = new TransactionService();
    this.LoanService = new LoanService();
  }

  componentDidMount = () => {
    this.fetchAccountsTotals();
    let { clientId } = this.props.match.params;

    this.setState({
      _id: clientId
    });
  };

  handleFormSubmit = event => {
    event.preventDefault();

    const _borrower = this.state._id;
    const collateralType = this.state.collateralType;
    const collateralValue = parseFloat(this.state.collateralValue);
    const collateralDescription = this.state.collateralDescription;
    const period = this.state.loanPeriod;
    const startDate = this.state.startDate;
    const duration = parseFloat(this.state.loanDuration);
    const capital = parseFloat(this.state.loanAmount);
    const interest = parseFloat(this.state.loanInterest);
    const loanType = this.state.loanType;
    const useOfFunds = this.state.useOfFunds;
    const toInvest = this.state.investors;
    console.log(interest);

    this.LoanService.createLoan(
      _borrower,
      collateralType,
      collateralValue,
      collateralDescription,
      period,
      startDate,
      duration,
      capital,
      interest,
      loanType,
      useOfFunds,
      toInvest
    )
      .then(response => {
        this.setState({
          _id: "",
          collateralType: "",
          collateralValue: 0,
          collateralDescription: "",
          startDate: "",
          loanDuration: 0,
          loanAmount: 0,
          loanInterest: 0,
          loanType: "linear",
          payment: 0,
          interestPmt: 0,
          capitalPmt: 0,
          open: false,
          loanFinalPayment: 0,
          loanInterestFinalPayment: 0,
          loanCapitalFinalPayment: 0,
          investor: "",
          useOfFunds: "",
          investmentAmount: 0,
          investors: []
        });
        this.props.history.push(`/loan/${response._id}`);
      })
      .catch(error => {
        this.setState({
          error: error
        });
      });
  };

  fetchAccountsTotals() {
    if (!this.state.accounts) {
      let country = this.props.userLocation;
      console.log(country);
      this.TransactionService.getInvestorsAvailability(country)
        .then(response => {
          this.setState({
            accounts: response
          });
        })
        .catch(err => {
          this.setState({
            accounts: false
          });
        });
    }
  }

  handleChange = event => {
    const { name, value } = event.target;
    this.setState({ [name]: value });
  };

  handleOpen = () => {
    this.setState({ open: true });
  };

  handleClose = () => {
    this.setState({ open: false });
  };

  handleSelectedInvestor = event => {
    const { name, value } = event.target;
    var index = this.state.accounts
      .map(function(e) {
        return e._id;
      })
      .indexOf(value);
    this.setState({ [name]: value });
    this.setState({
      investorAvailability: parseFloat(this.state.accounts[index].accumTotal)
    });
  };

  handleInvestment = event => {
    let { name, value } = event.target;
    console.log(value);
    if (isNaN(value)) {
      value = 0;
    }
    let index = this.state.accounts
      .map(function(e) {
        return e._id;
      })
      .indexOf(this.state.investor);
    let investorAvailability = parseFloat(
      this.state.accounts[index].accumTotal
    );
    let newAvailability = investorAvailability - parseFloat(value);
    let pct = parseFloat(value) / parseFloat(this.state.loanAmount);
    this.setState({ [name]: value });
    this.setState({ investorAvailability: newAvailability });
    this.setState({ pct: pct });
  };



  handleNewInvestor = () => {
    let investor = this.state.investor;
    let inv = parseFloat(this.state.investmentAmount);
    let pct = this.state.pct;
    let cashAccount = this.state.cashAccount;

    this.setState({
      investors: [
        ...this.state.investors,
        {
          _investor: investor,
          amount: inv,
          pct: pct,
          cashAccount: cashAccount
        }
      ],
      investor: "",
      investorAvailability: 0,
      pct: 0,
      investmentAmount: 0
    });
  };

  deleteInvestor = i => {
    let investorsD = this.state.investors;
    investorsD.splice(i, 1);
    this.setState({ investors: investorsD });
  };

  calcLoanSchedule = () => {
    let {
      _id,
      loanPeriod,
      loanDuration,
      loanInterest,
      loanAmount,
      startDate
    } = this.state;

    if (this.state.loanType === "linear") {
      let linear = linearLoan(
        _id,
        loanPeriod,
        loanDuration,
        loanInterest,
        loanAmount,
        startDate
      );
      this.setState({ loanSchedule: linear, open: true });
    } else if (this.state.loanType === "lumpSum") {
      let lumpSum = lumpSumLoan(
        _id,
        loanPeriod,
        loanDuration,
        loanInterest,
        loanAmount,
        startDate
      );
      this.setState({ loanSchedule: lumpSum, open: true });
    } else if (this.state.loanType === "payDay") {
      console.log('aqui')
      let payDay = payDayLoan(
        _id,
        loanPeriod,
        loanDuration,
        loanInterest,
        loanAmount,
        startDate
      );
      this.setState({ loanSchedule: payDay, open: true });
    }

  };

  render() {
    return (
      <div className="content">
        <form onSubmit={this.handleFormSubmit}>
        <h2 className=" display-6">NUEVO PRESTAMO</h2>
          <div className="form-row general-loan-details">
            <div className="card col-md-6">
              <div class="card-body">
                <h5 class="card-title">Datos básicos</h5>
                <div className="form-row">
                  <div className="form-group col-md-12">
                    <label>Uso de los fondos:</label>
                    <input
                      className="form-control"
                      type="text"
                      step="any"
                      name="useOfFunds"
                      value={this.state.useOfFunds}
                      onChange={e => this.handleChange(e)}
                      required
                    />
                  </div>
                </div>
                <div className="form-row">
                  <div className="form-group col-md-6 col-sm-6">
                    <label>Colateral:</label>
                    <select
                      className="form-control"
                      name="collateralType"
                      value={this.state.collateralType}
                      onChange={e => this.handleChange(e)}
                      required
                    >
                      <option value="NoCollateral">Sin Colateral</option>
                      <option value="automobiles">Automóvil</option>
                      <option value="electronic_items">
                        Equipos electronicos
                      </option>
                      <option value="insurance_policies">
                        Poliza de Seguro
                      </option>
                      <option value="investments">Inversiones</option>
                      <option value="machinery_and_equipment">
                        Maquinaria y Equipos
                      </option>
                      <option value="real_estate">Bienes Raices</option>
                      <option value="valuables_and_collectibles">
                        Valuables y coleccionables
                      </option>
                    </select>
                  </div>
                  <div className="form-group col-md-6 col-sm-6">
                    <label>Valor del Colateral:</label>
                    <input
                      className="form-control"
                      type="number"
                      step="any"
                      name="collateralValue"
                      value={this.state.collateralValue}
                      onChange={e => this.handleChange(e)}
                      required
                    />
                  </div>
                </div>
                <div className="form-group">
                  <label form="collateral_description">
                    Descripción del Colateral
                  </label>
                  <textarea
                    className="form-control col-md-12"
                    name="collateralDescription"
                    id="collateralDescription"
                    rows="4"
                    value={this.state.collateralDescription}
                    onChange={e => this.handleChange(e)}
                  />
                </div>
              </div>
            </div>
          </div>
          <div className="form-row general-loan-details">  
            <div className="card col-md-6">
              <div class="card-body">
                <h5 class="card-title">Detalles del prestamo</h5>
                <div className="form-row">
                  <div className="form-group col-md-6 col-sm-6">
                    <label>Fecha de Inicio</label>
                    <input
                      type="date"
                      className="form-control"
                      id="startDate"
                      name="startDate"
                      value={this.state.startDate}
                      onChange={e => this.handleChange(e)}
                      required
                    />
                  </div>
                  <div className="form-group col-md-6 col-sm-6">
                    <label>Frequencia:</label>
                    <select
                      id="loan_type"
                      className="form-control"
                      name="loanPeriod"
                      value={this.state.loanPeriod}
                      onChange={e => this.handleChange(e)}
                      required
                    >
                      <option defaultValue="monthly">Mensual</option>
                      <option value="biWeekly">Quincenal</option>
                      <option value="payDay">Dia de pago</option>
                    </select>
                  </div>
                </div>
                <div className="form-row">
                  <div className="form-group col-md-6 col-sm-6">
                    <label>Esquema:</label>
                    <select
                      id="loan_type"
                      className="form-control"
                      name="loanType"
                      value={this.state.loanType}
                      onChange={e => this.handleChange(e)}
                      required
                    >
                      <option defaultValue="linear">
                        Pagos Iguales + Interes + Sin Amort
                      </option>
                      <option value="payDay">
                        Dia de pago
                      </option>
                      <option value="lumpSum">
                        Final PMTs + Interest + No Amort
                      </option>
                    </select>
                  </div>
                  <div className="form-group col-md-6 col-sm-6">
                    <label>Principal:</label>
                    <input
                      className="form-control"
                      type="number"
                      step="any"
                      id="loanAmount"
                      name="loanAmount"
                      value={this.state.loanAmount}
                      onChange={e => this.handleChange(e)}
                      required
                    />
                  </div>
                </div>
                <div className="form-row">
                  <div className="form-group col-md-6 col-sm-6">
                    <label>Duracion:</label>
                    <input
                      className="form-control"
                      type="number"
                      step="any"
                      id="loanDuration"
                      name="loanDuration"
                      value={this.state.loanDuration}
                      onChange={e => this.handleChange(e)}
                      required
                    />
                  </div>
                  <div className="form-group col-md-6 col-sm-6">
                    <label>Tasa de Interes (M):</label>
                    <input
                      className="form-control"
                      type="number"
                      step="any"
                      id="loanInterest"
                      name="loanInterest"
                      value={this.state.loanInterest}
                      onChange={e => this.handleChange(e)}
                      required
                    />
                  </div>
                </div>
                <div className="form-row">
                  <div className="calc-button">
                    <button
                      type="button"
                      className="btn btn-info"
                      onClick={e => this.calcLoanSchedule(e)}
                    >
                      Cronograma
                    </button>
                    <Modal
                      aria-labelledby="simple-modal-title"
                      aria-describedby="simple-modal-description"
                      open={this.state.open}
                      onClose={this.handleClose}
                    >
                      <LoanSchedule loanSchedule={this.state.loanSchedule}/>
                    </Modal>
                  </div>
                </div>
              </div>
            </div>
          </div>
          <div className="form-row general-loan-details">
          <div className="card col-md-6">
            <div class="card-body">
              <h5 class="card-title">Inversionista</h5>
              <div className="form-row " id="participants">
                <div className="form-group col-md-6" id="investor-0">
                  <label>Cuenta Inversionista:</label>
                  <select
                    className="form-control"
                    name="investor"
                    value={this.state.investor}
                    onChange={e => this.handleSelectedInvestor(e)}
                  >
                    <option>Seleccionar Cuenta</option>
                    {this.state.accounts
                      ? this.state.accounts.map((e, i) => (
                          <option key={i} value={e._id}>
                            {e.investor[0].firstName +
                              " " +
                              e.investor[0].lastName +
                              " " +
                              e.investor[0].location}
                          </option>
                        ))
                      : ""}
                  </select>
                </div>
                <div className="form-group col-md-6" id="investor-0">
                  <label>Cuenta de efectivo:</label>
                  <select
                    className="form-control"
                    name="cashAccount"
                    value={this.state.cashAccount}
                    onChange={e => this.handleChange(e)}
                  >
                    <option>Seleccionar Cuenta</option>
                    {this.state.cashAccounts
                      ? this.state.cashAccounts.map((e, i) => (
                          <option key={i} value={e._id}>
                            {e}
                          </option>
                        ))
                      : ""}
                  </select>
                </div>
                <div className="form-group col-md-6">
                  <label>Inversion:</label>
                  <input
                    className="form-control"
                    type="text"
                    step="any"
                    name="investmentAmount"
                    value={this.state.investmentAmount}
                    onChange={e => this.handleInvestment(e)}
                  />
                </div>
                <div className="form-group col-md-3">
                  <label>Disponible:</label>
                  <input
                    disabled
                    className="form-control"
                    type="text"
                    step="any"
                    name="availableCash"
                    value={this.state.investorAvailability}
                  />
                </div>
                <div className="form-group col-md-3">
                  <label>Porcentage:</label>
                  <input
                    disabled
                    className="form-control"
                    type="text"
                    step="any"
                    name="pct"
                    value={this.state.pct}
                  />
                </div>
              </div>
              <div className="add_inv form-group align-bottom">
                <button
                  type="button"
                  className="btn btn-success align-bottom"
                  onClick={() => this.handleNewInvestor()}
                >
                  Agregar Inversionista
                </button>
              </div>
            </div>

          <div class="card-body">
          <table className="table">
            <thead>
              <tr>
                <th scope="col">Inversionista</th>
                <th scope="col">Efectivo</th>
                <th scope="col">Monto</th>
                <th scope="col">Porcentaje</th>
                <th scope="col">Acción</th>
              </tr>
            </thead>
            <tbody>
              {this.state.investors.length > 0 ? (
                this.state.investors.map((e, i) => {
                  return (
                    <tr key={i}>
                      <td>{e._investor}</td>
                      <td>{e.cashAccount}</td>
                      <td>{e.amount}</td>
                      <td>{e.pct}</td>
                      <td>
                        <button
                          type="button"
                          className="btn btn-warning"
                          onClick={i => this.deleteInvestor(i)}
                        >
                          X
                        </button>
                      </td>
                    </tr>
                  );
                })
              ) : (
                <tr>
                  <td>Agregue Inversionista</td>
                </tr>
              )}
            </tbody>
          </table>
          </div>
          </div>
          </div>
          <div className="form-row general-loan-details">
          <div className="card col-md-6">
          <div class="card-body">
          <div>
            <button type="submit" className="btn btn-info" id="submit_button">
              Crear
            </button>
          </div>
          </div>
          </div>
          </div>
        </form>
      </div>
    );
  }
}

export default withRouter(CreateLoan);
